import { moduleMap } from "./modules";

/**
 * Technical component for lazy loading a webcomponent module.
 *
 * Checks for <iapps-module name="${module}"> subcomponents and loads the requested modules.
 */
export class RequireModuleComponent extends HTMLElement {
  constructor(private remoteModules = moduleMap) {
    super();
  }

  public connectedCallback() {
    const observer = new MutationObserver(this.loadModules.bind(this));
    observer.observe(this, { childList: true });
    this.loadModules();
  }

  private async loadModules() {
    this.querySelectorAll("iapps-module").forEach(module => {
      if (!module.hasAttribute("name")) {
        return;
      }

      const loadFunction = this.remoteModules[module.getAttribute("name") as string];
      if (!loadFunction) {
        this.dispatchEvent(new CustomEvent("error", { detail: "Unknown module " + module.getAttribute("name") }));
        return Promise.reject();
      }
      return loadFunction();
    });
  }
}

customElements.define("iapps-require-module", RequireModuleComponent);
