/**
 * Module for determining and setting the public path for webpack.
 */

const IAPP_GENERICS_SRC = /iapp-generics\.(\w+\.)*js(\?.*)?$/;

/**
 * Determine the webpack public path (https://webpack.js.org/guides/public-path/) from the iapp-generics source.
 *
 * All lazy loaded chunks are expected to reside in the same location as iapp-generics, so by identifying the
 * iapp-generics script tag and extracting the base tag, the public path can be set dynamically.
 *
 * In order to identify the correct base path the following logic should be applied:
 * - find a script with the id "iapp-generics" and a src attribute and strip the base base path
 * - if this could not be found, search for a script tag including iapp-generics.js and use this base tag
 *
 * if this fails, undefined should be returned.
 */
export function determinePublicPath() {
  const explicitDefinition = document.querySelector("script#iapp-generics[src]") as HTMLScriptElement;
  if (explicitDefinition) {
    return determineBasePath(explicitDefinition.src);
  }

  const scripts: NodeListOf<HTMLScriptElement> = document.querySelectorAll("script[src]");
  // disabled due to ES5 for-of not supported in ES5 (IE)
  // tslint:disable-next-line:prefer-for-of
  for (let idx = 0; idx < scripts.length; idx++) {
    const srcPath = scripts[idx].src;
    if (!IAPP_GENERICS_SRC.test(srcPath)) {
      continue;
    }

    return determineBasePath(srcPath);
  }
}

/**
 * Determine the base path of a scrip src and return it.
 *
 * @param srcPath The raw path to extract the base path from.
 */
function determineBasePath(srcPath: string) {
  const path = srcPath
    .replace(/^http(s)?:\/\/.*?\//i, "")
    .split("?", 2)[0]
    .split("/")
    .slice(0, -1)
    .join("/");
  if (!path) {
    return "/";
  }
  return `/${path}/`;
}

if (!(window as KarmaTestContextWindow).__karma__) {
  const publicPath = determinePublicPath();
  if (publicPath) {
    __webpack_public_path__ = publicPath;
  }
}

type KarmaTestContextWindow = any;
